@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:100,300,600,700&display=swap');

* {
    margin: 0;
    padding:0;
    font-family: 'Libre Franklin', sans-serif;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.heading-icon {
    margin: 3rem;
}

.heading-text {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    font-weight:700;
}

.light-text {
    font-weight: 100;
    color: hsl(0, 0%, 59%);
}

p {
    margin-bottom: 1.5rem;
}

.input {
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    position: relative;
}

input {
    padding: 13px;
    width: 20rem;
    border-radius: 2rem;
    border: 1px solid  hsl(223, 100%, 88%);
    outline: none;
    color:  hsl(223, 87%, 63%);
}

input::placeholder {
    color:  hsl(223, 100%, 88%);
    letter-spacing: 1px;
}


button {
    padding:14px 40px;
    border-radius: 2rem;
    color: white;
    background-color:  hsl(223, 87%, 63%);
    border: none;
    cursor: pointer;
}

button:hover {
    background-color: hsl(223, 100%, 88%);
}

.img {
    width: 700px;
    margin: 5rem;
}

.icons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
    gap: 20px;
}

.icon {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    border-radius: 50%;
    outline: none;
    color: hsl(223, 87%, 63%);   
}

.icon:hover {
    outline: 1px solid white;
}

.insta:hover {
    background:linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
    color: white;
    
}

.fb:hover {
    background-color: hsl(223, 87%, 63%);
    color: white;
   
}

.x:hover {
    background-color: black;
    color: white;
}

.error-text {
    position: absolute;
    margin-left: 90px;
    margin-top: 10px;
    font-style: italic;
    color: red;
}

